.footer {
  background-color: $rose-section-color;
}

.foot_button {
  margin-right: auto;
  margin-left: auto;
  padding-top: 54px;
  padding-bottom: 113px;
  & li {
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
  }
  & :nth-last-child(1) {
    margin-bottom: 0;
  }
}
.foot_button-location {
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 21px;
  letter-spacing: 0em;
  text-align: center;
  background-color: #d41342;
  width: 161px;
  height: 44px;
  border-radius: 22px;
  color: $white-color;
}

.button_sign-white {
  stroke: $white-color;
  margin-left: 5px;
  width: 7px;
  height: 11px;
}

.foot_button-franchise {
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 21px;
  letter-spacing: 0em;
  text-align: center;
  background-color: $white-color;
  width: 161px;
  height: 44px;
  border-radius: 22px;
  color: $accent-red-color;
  transition: $trans-duration $trans-time-func;
}
.foot_button-franchise:hover {
  background-color: $icon-bkg-color2;
  color: $accent-red-color;
}
.foot_button-franchise:focus {
  background-color: $icon-bkg-color2;
  color: $accent-red-color;
}

.button_sign-red {
  fill: var(--color1);
  margin-left: 8px;
  width: 7px;
  height: 10px;
}
.foot-title {
  width: 234px;
  height: 135px;
}

.foot_header {
  text-transform: uppercase;
  color: $accent-red-color;
  font-family: Titan One;
  font-size: 34px;
  font-style: normal;
  font-weight: 400;
  line-height: 39px;
  letter-spacing: 0.04em;
  text-align: left;
  margin-top: 20px;
}
.foot_pre-header {
  font-family: Titan One;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: 0em;
  text-align: left;
  text-transform: uppercase;
  color: $rose-text-color;
}

.foot_contacts {
  display: flex;
  justify-content: flex-end;
  padding-bottom: 43px;
  margin-right: auto;
  margin-left: auto;
  background-color: $rose-section-color;

  &-phone {
    font-family: DM Sans;
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px;
    letter-spacing: 0em;
    text-align: right;
    color: $accent-red-color;
  }
  &-description {
    font-family: DM Sans;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: 0em;
    text-align: right;
    color: $black-color;
  }
}

.foot_link {
  display: flex;
  width: 32px;
  height: 32px;
  background-color: $icon-bkg-color;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.foot_link:hover {
  background-color: $accent-red-color;
}
.foot_link:focus {
  background-color: $accent-red-color;
}
.foot_icon {
  width: 19px;
  height: 19px;
}
.foot_sign {
  display: flex;
  justify-content: center;
  background-color: $rose-section-color;
  margin-right: auto;
  margin-left: auto;
  border-top: solid 1px #e1e1e1;
  padding-top: 25px;
  padding-bottom: 25px;
  padding-left: 20px;
  padding-right: 20px;

  &-text {
    display: block;
    width: 133px;
    font-family: DM Sans;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 21px;
    letter-spacing: 0em;
    text-align: center;
    color: $secondary-text-color;
  }
}
.foot_list {
  display: flex;
  justify-content: flex-end;
  padding-bottom: 46px;
  padding-top: 43px;

  &-item:not(:last-child) {
    margin-right: 28px;
  }
}
